import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ListaActividadesComponent } from './pages/lista-actividades/lista-actividades.component';
import { ListaDetalleActividadComponent } from './pages/lista-detalle-actividad/lista-detalle-actividad.component';
import { DashboardActividadesComponent } from './pages/dashboard-actividades/dashboard-actividades.component';
import { RoleGuard } from 'src/app/core/guards/role.guard';
import { Role } from 'src/app/enum/role.enum';

const routes: Routes = [
  { path: 'lista', component: ListaActividadesComponent},
  { path: 'lista-actividades', component: DashboardActividadesComponent,
  canActivate:[RoleGuard], data: { roles: [Role.ROLE_ESTUDIANTE] },},
  { path: ':id', component: ListaDetalleActividadComponent, canActivate:[RoleGuard], data: { roles: [Role.ROLE_ESTUDIANTE] },}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActividadRoutingModule { }
