import { Component, OnInit, ViewChild } from '@angular/core';
import { Actividad, ActividadEstado } from '../../models/actividad';
import { ActividadService } from '../../service/actividad.service';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { isDescription } from 'src/app/constantes/constantesproyecto';
import Swal from 'sweetalert2';
import { UsuarioA } from 'src/app/account/auth/login/models/usuario';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';
import { Role } from 'src/app/enum/role.enum';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-lista-actividades',
  templateUrl: './lista-actividades.component.html',
  styleUrls: ['./lista-actividades.component.scss']
})
export class ListaActividadesComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  actividades: Actividad[] = [];
  actividadesE: Actividad;
  actividadModi:Actividad;
  formActividad!: FormGroup;
  userauth!: UsuarioA
  estadoActividadModidicar:ActividadEstado;
  roleUser:string;
  estadoParam: string = "Activo";
  isSuperAdmin: boolean;
  isAdminRedes:boolean;
  showLoading = false;
  //------------------
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any = {};
  dtTrigger: Subject<Actividad> = new Subject<Actividad>();

  constructor(private actividadServicio: ActividadService,
    private fb: FormBuilder,
    private authService: AutenticacionService,
    private modalService: NgbModal,) {
    this.formActividad = this.iniciarFormulario();

  }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Actividad' }, { label: 'Registrar', active: true }];
    this.userauth = this.authService.getUserFromLocalCache();
    this.roleUser=this.userauth.role;
    this.dtOptions = {
      columnDefs: [
        // { className: "", targets: [0, 1,2] },
        { orderable: false, targets: [0, 3] },
        { searchable: false, targets: [0, 3] },
        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };

    this.getActividadesRegistradas();

  }

  getPermisoDetalle(){
    if(this.roleUser!=null){
        if(this.roleUser==Role.ROLE_ADMIN_REDES){
          return this.authService.hasRole(this.roleUser);
        }else if(this.roleUser==Role.ROLE_SUPER_ADMIN){
          return this.authService.hasRole(this.roleUser);
        }else if(this.roleUser==Role.ROLE_PASANTE){
          return this.authService.hasRole(this.roleUser);
        }else{
          // no hace nada
        }
    }

  }

  ngOnDestroy() { //para destruir la peticion
    this.dtTrigger.unsubscribe();
  }

  openModalA(content) {
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg'});
  }

  private iniciarFormulario(): FormGroup {
    return this.fb.group({
      id:['',],
      descripcion: ['', [Validators.required, Validators.minLength(5), Validators.pattern(isDescription)]],

    });
  }

  esCampoValido(campo: string) {
    const validarCampo = this.formActividad.get(campo);
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }

  setDatos(actividae: Actividad) {
    this.actividadModi= actividae;
    this.formActividad.patchValue(actividae);
  }

  guardarActividad() {
    if (this.formActividad.valid) {
      if (this.actividadModi!=null) {
        this.editandoActividadForm();
      } else {
        this.registrandoActividadForm();
      }

    } else {
      Swal.fire({
        confirmButtonColor: "#a90000",
        position: 'center',
        title: 'Faltan datos en el formulario',
        text: 'completar campos requeridos',
        icon: 'warning',
      });
    }

  }

  registrandoActividadForm() {
    this.showLoading = true
    const actividad = this.formActividad.get('descripcion').value
    const estado="Activo";
    this.actividadesE = new Actividad(estado,actividad, this.userauth.id)

    this.actividadServicio.nuevaActividad(this.actividadesE).subscribe((response: any) => {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Éxito',
        text: `Almacenado correctamente`,
        icon: 'success',
      });
      this.formActividad.reset();

      this.reload();
      this.getActividadesRegistradas();
    }, (err: any) => {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Advertencia',
        text: `Ocurrio un problema`,
        icon: 'warning',
      });
    });

  }

  editandoActividadForm(){
    this.showLoading = true
    if(this.actividadModi.estado=="Activo"){
      const id= this.formActividad.get('id').value
      const estado="Activo";
      const actividadE = this.formActividad.get('descripcion').value
      this.actividadModi= new Actividad(estado,actividadE,this.userauth.id,id);
      this.actividadServicio.editarActividad(this.actividadModi).subscribe((response:any)=>{
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Éxito',
          text: `Modificado correctamente`,
          icon: 'success',
        });

        this.formActividad.reset();
        this.reload();
        this.getActividadesRegistradas();
      },(err: any) => {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Advertencia',
          text: `Ocurrio un problema`,
          icon: 'warning',
        });
      });

    }else{
        //si no es porque es inactivo
        const id= this.formActividad.get('id').value
      const estado="Inactivo";
      const actividadE = this.formActividad.get('descripcion').value
      this.actividadModi= new Actividad(estado,actividadE,this.userauth.id,id);
      this.actividadServicio.editarActividad(this.actividadModi).subscribe((response:any)=>{
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Éxito',
          text: `Modificado correctamente`,
          icon: 'success',
        });

        this.formActividad.reset();
        this.reload();
        this.getActividadesRegistradas();
      },(err: any) => {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Advertencia',
          text: `Ocurrio un problema`,
          icon: 'warning',
        });
      });
    }

  }


  /**Para recargar */
  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      //this.actividades = []
      dtInstance.destroy()
      //this.getActividadesRegistradas()
    })
  }

  getActividadesRegistradas() {
    this.actividadServicio.actividadesRegistradas(this.userauth.id).subscribe((resp: Actividad[]) => {
      this.actividades = resp

      // Verificamos si el usuario tiene el rol ROLE_SUPER_ADMIN
      this.isSuperAdmin = this.userauth.role === 'ROLE_SUPER_ADMIN';
      // Verificamos si el usuario tiene el rol ROLE_ADMIN_REDES
      this.isAdminRedes = this.userauth.role === 'ROLE_ADMIN_REDES';
      this.dtTrigger.next(null)
      this.actividadModi=null;
    })
  }

    estados(event: any) {
    this.estadoParam = event.target.value;
    this.cargarConEstadoAct();
  }

  cargarConEstadoAct() {
    this.reload();
    this.actividadServicio.actividadesRegistradasConEstado(this.estadoParam,this.userauth.id)
    .subscribe((response) => {
          if (this.estadoParam === "todos" || this.estadoParam === "Seleccione") {
            this.actividades = [];
            this.getActividadesRegistradas();
          } else {
            this.actividades = response;
            // Verificamos si el usuario tiene el rol ROLE_SUPER_ADMIN
            this.isSuperAdmin = this.userauth.role === 'ROLE_SUPER_ADMIN';
            // Verificamos si el usuario tiene el rol ROLE_ADMIN_REDES
            this.isAdminRedes = this.userauth.role === 'ROLE_ADMIN_REDES';
            this.dtTrigger.next();
          }
        });
  }


}
