import { Component, OnInit, ViewChild } from '@angular/core';
import { Actividad } from '../../models/actividad';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { ActividadService } from '../../service/actividad.service';
import { UsuarioA } from 'src/app/account/auth/login/models/usuario';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';


@Component({
  selector: 'app-dashboard-actividades',
  templateUrl: './dashboard-actividades.component.html',
  styleUrls: ['./dashboard-actividades.component.scss']
})
export class DashboardActividadesComponent implements OnInit {

  breadCrumbItems: Array<{}>;
  actividades:Actividad[]=[];
  userauth!: UsuarioA
  idEstudiantePersona:number;
  showLoading = false;

     //------------------
    /**DataTables */
    @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
    dtOptions: any = {};
    dtTrigger: Subject<Actividad> = new Subject<Actividad>();

  constructor(private actividadServicio:ActividadService,
    private authService: AutenticacionService) {
     }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Actividades' }, { label: 'Detalles', active: true }];
    this.userauth = this.authService.getUserFromLocalCache();
    this.idEstudiantePersona=this.userauth.estudiante.id;

    this.dtOptions = {
      columnDefs: [
        { className: "", targets: [0, 1, 2, 3]},
        { orderable: false, targets: [4] },
        { searchable: false, targets: [0,4] },

        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };
    this.cargarDatosActividadesConHoras();

   }

  ngOnDestroy() { //para destruir la peticion
    this.dtTrigger.unsubscribe();
  }

  cargarDatosActividadesConHoras() {
    this.actividadServicio.mostrarActividadesListEstudiante(this.idEstudiantePersona).subscribe((resp: any) => {
      this.actividades = resp;
      this.dtTrigger.next(null);
    });

  }


    /**Para recargar */
reload() {
  this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
    this.actividades = []
    dtInstance.destroy()
    this.cargarDatosActividadesConHoras()
  });
}

descargarPdfActividades() {
  this.showLoading = true;
  this.actividadServicio.generarPdfActividadesRealizadas(this.idEstudiantePersona)
    .subscribe((data: Blob) => {
      this.showLoading = false;
      const blob = new Blob([data], { type: "application/pdf" });
      const link = document.createElement("a");
      let url = URL.createObjectURL(blob);
      window.open(url);
    });
}

}
